@media screen and (max-width: 812px) {    .Footer {        display: block;        padding-top: 20px;        .menu {            align-items: start;            flex-direction: column;        }    }    .footer-contacts {        width: 100%;        display: block;    }    .mobile {        display: none !important;    }    .footer-box {        &:nth-of-type(2), &:nth-of-type(3)  {            width: 50%;            display: inline-block;        }    }    .social {        margin-top: 10px;        img {            margin: 0 8px 0 0;        }    }}