@import '../app/_mixins.scss';
@import '../app/_fonts.scss';

.main {
  margin: 25px 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  grid-gap: 5vw;
}

.small-box {
  text-align: center;

  &:last-child{
    grid-column: 1 / 3;
    width: 50%;
    margin: auto;
  }
}

.block__main-heading {
  color: var(--blue);
  @include font('PT Serit', 36px);
}

.block__text {
  color: var(--black);
}

.block__text, .block__a-btn {
  @include font('DecimaNovaProLt', 24px);
}

.block__a-btn {
  background-color: var(--blue);
  color: var(--white);
  padding: 16px 86px;
  border-radius: 40px;
  &:hover {
    text-decoration: none;
    transition: all .4s ease;
    background-color: #fff;
    border: 1px solid var(--blue);
    color: var(--blue);
  }
}

.a-btn {
  color: var(--blue);
  @include font('DecimaNovaProLt', 24px);
  border-radius: 25px;
  border: 1px solid var(--blue);
  padding: 10px 50px;
  &:hover {
    text-decoration: none;
    transition: all .4s ease;
    background-color: var(--blue);
    color: #fff;
  }
}

.box-heading {
  margin: 0;
  line-height: 1.1;
}

.intro__p {
  margin-top: 40px;
}

.Block {
  .mobile-show {
    display: none;
  }
}