@import '../app/_mixins.scss';
@import '../app/_fonts.scss';

.Footer {
  background-color: var(--neavy-blue);
  border-top: 4px solid #00abff;
  height: 100%;
  min-height: 239px;
  .menu {
    align-items: center;
  }
}



.menu ul {
  padding: 0;
}

.copy-rights, .footer-contacts {
  color: white;
  margin-bottom: 0;
}

.footer-contacts {
  width: 60%;
  height:100%;
  min-height: 145px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer-box {
  @include font ('DecimaNovaProLt', 16px);
  line-height: 1;
}

.span-email {
  color: #0097e3;
}

.footer-menu {
  display: flex;
  align-items: center;
  border-top: 1px solid #002545;
  height:100%;
  min-height: 93px;
}



.social {
  img {
    border-radius: 100%;
    border: 1px solid #fff;
    padding: 2px 7px;
    margin: 0 8px;
    &:hover {
      background-color: var(--blue);
      border: 1px solid var(--blue);
      transition: all .4s ease;
    }
  }
}
