@import '../app/_mixins.scss';

.SecondHeader {
  @include header(#00172b, 59px);
  align-items: center;
  display: flex;
  border-top: 1px solid #00203c;
}

.second-header__ul {
  display: flex;
  padding:0;
  li {
    a {
      &:hover {
        padding: 0 0 18px;
        border-bottom: 4px solid var(--blue);
      }
    }
  }
}

.mob {
  display: none;
}