$neavyblue: #00172b;
$blue: #00abff;

@mixin header ($back_color, $height) {
  background-color: $back_color;
  height: $height;
}

@mixin header-hover ($color, $text-decoration) {
  color: $color;
  text-decoration: $text-decoration;
}

@mixin font ($font-family, $font-size) {
  font-family: $font-family;
  font-size: $font-size;
}

