@media screen and (max-width: 980px) {    .MainHeader, .IntroBox, .SecondHeader, .Footer {        .container {            width: 100%;            padding-right: 15px;            padding-left: 15px;            margin-right: 19px;            margin-left: 19px;            max-width: 960px;        }    }    .Box, .Block {        .container {            width: 100%;            padding-right: 15px;            padding-left: 15px;            margin: auto;            max-width: 960px;        }    }    .dekstop {        display: block;    }    .mobile-show {        display: none;    }}@media screen and (max-width: 812px)  {   .IntroBox, .SecondHeader{        .container {            max-width: 720px !important;            margin: auto;        }    }}