@media screen and (max-width: 980px) {
  .box-heading {
    font-size: 48px;
  }

  .box-image {
    max-width: 327px;
    max-height: 273px;
  }
}

@media screen and (max-width: 812px) {
  .nav-links {
    height: 0;
    overflow: hidden;
    transition: all .6s ease;
    list-style-type: none;
    ul {
      margin-top: 50px;
    }
    li {
      display: block;
    }
  }

  .show-nav {
    height: 390px;
    background-color: var(--neavy-blue);
    position: absolute;
    top: 4px;
    bottom: 0;
    right: 0;
    left: 0;
  }

  .nav-btn {
    display: block;
  }

  button {
    background-color: var(--neavy-blue);
    border: 1px solid transparent;
  }

  .nav-icon {
    color: #fff;
  }

  button, .logo {
    z-index: 9999;
    position: relative;
  }

  .menu-812px-main {
    flex-flow: row wrap;
    ul {
      li {
        text-align: left;
        padding: 25px 22px 0;
      }
    }
  }

  .menu {
    ul {
      li {
        padding: 1rem 1rem;
      }
    }
  }


}
