@import '../app/_mixins.scss';
@import '../app/_fonts.scss';

.MainHeader {
  border-top: 4px solid #00abff;
  @include header(#00172b, 65px);
  align-items: center;
  display: flex;
  border-bottom: 1px solid #00203c;
}

.menu {
  margin: 0;
  width: 100%;
  height: 100%;
  ul {
    margin: 0;
    li {
      list-style: none;
      margin: 0;
      padding: .2rem 1rem;
      font-family: 'DecimaNovaProLt';
      font-weight: 700;
      text-transform: uppercase;
      &:hover {
        background-color: transparent!important;
      }

      a {
        color: #fff;
        font-size: 14px;
        &:hover {
          @include header-hover($blue, none);
        }
      }
    }
  }
}

.ul-list  {
  li {
    &:last-child {
      border-radius: 40px;
      border:1px solid #fff;
      padding: 5px 20px;
      &:hover {
        border:1px solid #00abff;
        background-color: var(--blue);
        color: #fff;
        transition: all .4s ease;
      }
    }
  }
}

.nav-btn {
  display: none;
}

.nav-links {
  width: 100%;
  text-align: right;
  li {
    display: inline-block;
  }
}

.IntroBox {
  .p-heading {
    font-size: 38px;
  }
}