@import '../app/_mixins.scss';
@import '../app/_fonts.scss';

.Box {
  background-image: url("../box/img/line.png");
  background-position: bottom;
  background-size: contain;
  background-repeat: no-repeat;
}

.intro {
  min-height: 267px;
  height: 100%;
  text-align: center;
  padding: 63px 0;
  width: 100%;
}

.box-heading {
  color: var(--text-blue);
  @include font('PT Serit', 54px);
}

.small-heading {
  line-height: inherit;
  @include font('DecimaNovaProLt', 28px);
}

