@media screen and (max-width: 980px){    .Block {        .intro {            margin: 0 142px;        }        .dekstop {            display: block;        }        .mobile-show {            display: none;        }    }}@media screen and (max-width: 812px)  {    .main {        display: block;    }    .box-heading {        font-size: 38px;    }    .small-box, .small-box:last-child {        width: 100%;        margin: auto;    }    .Block {        .intro {            margin: auto;        }        .dekstop {            display: none;        }        .mobile-show {            display: block;        }    }}